<div class="poster"><div class="frame" :class="[activeId]"></div><div class="link" v-for="(item, key) in list" @click="handleActiveId(item.id)" :class="item.id" :key="key"></div></div><div class="section-wrapper"><section v-for="(item, key) in list" :ref="setSectionRef" :id="item.id" :key="key"><div class="name"><span v-for="(name, key) in item.character.split(' ')" :key="key">{{ name }}</span></div><div class="character-img" :style="{backgroundImage: `url(${item.img})`}"></div><div class="desc" v-html="item.desc"></div></section></div>